.wrapperText {
    color: #fff;
    text-align: center;
    font-size: 15px;
    font-family: 'Montserrat', sans-serif;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 10;
    width: 90%;
    max-width: 700px;
    padding: 20px;
    box-sizing: border-box;
    background: rgba(0, 0, 0, 0.45); /* –ø–æ–º–æ–≥–∞–µ—Ç —á–∏—Ç–∞–µ–º–æ—Å—Ç–∏ –Ω–∞ —Å–≤–µ—Ç–ª–æ–º —Ñ–æ–Ω–µ */
    border-radius: 10px;
  }
  
  .title {
    font-weight: bold;
    font-size: 34px;
    margin-bottom: 30px;
  }
  
  .text {
    font-size: 16px;
    line-height: 1.6;
    margin-bottom: 15px;
  }
  
  /* –ü–ª–∞–Ω—à–µ—Ç */
  @media (max-width: 768px) {
    .title {
      font-size: 28px;
    }
    .text {
      font-size: 15px;
    }
  }
  
  /* –ú–æ–±–∏–ª—å–Ω—ã–π */
  @media (max-width: 480px) {
    .title {
      font-size: 22px;
    }
    .text {
      font-size: 14px;
    }
  }
  