.wrapper {
    width: 100%;
    height: 90vh;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 15px; /* —á—Ç–æ–±—ã –Ω–µ –ø—Ä–∏–∂–∏–º–∞–ª–æ—Å—å –∫ –∫—Ä–∞—è–º */
}

.insideWrapper {
    width: 677px;
    max-width: 100%;
}

.title {
    font-weight: bold;
    font-family: 'Montserrat';
    font-size: 40px;
    text-align: center;
    margin-bottom: 32px;
}

.subTitle {
    font-size: 15px;
    font-family: 'Montserrat';
    text-align: center;
}

.number {
    font-weight: bold;
    font-family: 'Montserrat';
    text-decoration: underline;
    font-size: 24px;
    width: 100%;
    text-align: center;
    margin-bottom: 16px;
}

.but {
    border-radius: 40px;
    border: 3px solid #1235a6;
    width: 425px;
    height: 34px;
    font-family: 'Montserrat';
    font-size: 16px;
    color: #1235a6;
}

.buttonWrapper {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 80px;
}

.but:hover {
    background-color: #1235a6;
    color: #fff;
}

.mail, .mail1 {
    font-family: 'Montserrat';
    font-size: 15px;
}

.mail span,
.mail1 span {
    font-weight: bold;
}

.contactWrapper1,
.contactWrapper2 {
    width: 49%;
}

.contactWrapper1 {
    text-align-last: left;
}

.contactWrapper2 {
    text-align-last: right;
}

.contactsWrapper {
    display: flex;
    flex-direction: row;
    gap: 10px;
}

.line {
    border-top: 1px solid #c4c4c4;
    margin-bottom: 25px;
}

.adress {
    text-align: center;
}

.inn {
    text-align: center;
}

.mail1 {
    text-align: center;
    margin-top: 20px;
    margin-bottom: 32px;
}

.adressWrapper {
    font-family: 'Montserrat';
    margin-bottom: 46px;
    margin-top: 54px;
}

.adress span {
    font-weight: bold;
    font-size: 15px;
}

/* –ú–µ–¥–∏–∞–∑–∞–ø—Ä–æ—Å—ã */

/* –ü–ª–∞–Ω—à–µ—Ç—ã –∏ –º–µ–Ω—å—à–µ */
@media (max-width: 768px) {
    .insideWrapper {
        width: 90%;
    }

    .title {
        font-size: 32px;
        margin-bottom: 24px;
    }

    .number {
        font-size: 20px;
        margin-bottom: 14px;
    }

    .mail, .mail1 {
        font-size: 14px;
    }

    .adressWrapper {
        margin-top: 40px;
        margin-bottom: 36px;
    }

    .contactsWrapper {
        gap: 8px;
    }
}

/* –ú–æ–±–∏–ª—å–Ω—ã–µ —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞ (–¥–æ 480px) */
@media (max-width: 480px) {
    .title {
        font-size: 24px;
        margin-bottom: 16px;
    }

    .subTitle {
        font-size: 14px;
    }

    .number {
        font-size: 18px;
        margin-bottom: 12px;
    }

    .mail, .mail1 {
        font-size: 13px;
    }

    .adressWrapper {
        margin-top: 30px;
        margin-bottom: 24px;
    }

    .contactsWrapper {
        flex-direction: column;
        gap: 16px;
    }

    .contactWrapper1,
    .contactWrapper2 {
        width: 100%;
        text-align-last: center;
        text-align: center;
    }
}
