.email {
    font-family: 'Montserrat';
    font-size: 24px;
    text-decoration: underline;
    font-weight: bold;
    text-align: center;
    margin-top: 19px;
}

.wrapper {
    background-color: #e4e4e4;
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 15px; /* –Ω–µ–±–æ–ª—å—à–æ–π –æ—Ç—Å—Ç—É–ø –ø–æ –±–æ–∫–∞–º –¥–ª—è –º–∞–ª–µ–Ω—å–∫–∏—Ö —ç–∫—Ä–∞–Ω–æ–≤ */
}

.insideWrapper {
    width: 677px;
    max-width: 100%; /* —á—Ç–æ–±—ã –Ω–µ –≤—ã—Ö–æ–¥–∏—Ç—å –∑–∞ –ø—Ä–µ–¥–µ–ª—ã —ç–∫—Ä–∞–Ω–∞ */
}

.title {
    font-family: 'Montserrat';
    font-size: 40px;
    font-weight: bold;
    text-align: center;
    margin-bottom: 21px;
}

.subTitle {
    font-size: 15px;
    font-family: 'Montserrat';
    text-align: center;
}

.phoneWrapper {
    display: flex;
    flex-direction: row;
    gap: 5%;
    justify-content: center;
    align-items: center;
    margin-top: 80px;
    margin-bottom: 23px;
}

.phone {
    font-size: 24px;
    font-family: 'Montserrat';
    font-weight: bold;
}

/* –ú–µ–¥–∏–∞–∑–∞–ø—Ä–æ—Å—ã –¥–ª—è –∞–¥–∞–ø—Ç–∏–≤–Ω–æ—Å—Ç–∏ */

/* –ü–ª–∞–Ω—à–µ—Ç—ã –∏ –º–µ–Ω—å—à–µ (–¥–æ 768px) */
@media (max-width: 768px) {
    .insideWrapper {
        width: 90%;
    }

    .title {
        font-size: 32px;
        margin-bottom: 16px;
    }

    .phoneWrapper {
        margin-top: 40px;
        margin-bottom: 20px;
        gap: 10%;
    }

    .phone {
        font-size: 20px;
    }
}

/* –ú–æ–±–∏–ª—å–Ω—ã–µ —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞ (–¥–æ 480px) */
@media (max-width: 480px) {
    .title {
        font-size: 24px;
        margin-bottom: 12px;
    }

    .subTitle {
        font-size: 14px;
    }

    .phoneWrapper {
        flex-direction: column;
        gap: 12px;
        margin-top: 30px;
        margin-bottom: 20px;
    }

    .phone {
        font-size: 18px;
    }

    .email {
        font-size: 20px;
        margin-top: 15px;
    }
}
